const createRequestInit = (text: string) => (
    {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({
            "username": "Sinkbot",
            "embeds": [
                {
                    "title": text,
                    "color": parseInt("336699", 16),
                }
            ]
        })
    }
)

export async function sendDiscord(url: string, text: string) {
    await fetch(new Request(url, createRequestInit(text)))
}