import React from 'react';
import Button from '@material-ui/core/Button'
import Box from '@material-ui/core/Box'
import { IHistory } from '../../../interfaces/IHistory';
import { PushPin } from '../svg/PushPin';
import { PushedPin } from '../svg/PushedPin';
import { Play } from '../svg/Play';
import { useTheme } from '@material-ui/core';

interface Props {
    task: IHistory
    actHandler: (taskName: string) => void
    pinHandler: (taskName: string) => void
    margin: number
}


export function Task(props: Props) {
    const { task, actHandler, pinHandler, margin } = props;
    const theme = useTheme();
    const cssProps: React.CSSProperties = {
        fill: theme.palette.primary.main,
    }
    const taskProps: React.CSSProperties = {
        flexGrow: 1,
        margin: "auto",
        lineBreak: "anywhere",
    }
    return (
        <Box className="task" display="flex" justifyContent="space-between" marginY={`${margin}px`}>
            <Button onClick={() => { return actHandler(task.taskName) }}>
                <Play />
            </Button>
            <Button onClick={() => { return pinHandler(task.taskName) }} color={task.isPinned ? "primary" : "default"}>
                {task.isPinned ? <Box style={cssProps} height="24px"> <PushedPin /></Box> : <PushPin />}
            </Button>
            <Box style={taskProps}><Box>{task.taskName}</Box></Box>
        </Box>
    );
}
