import React, { Dispatch, SetStateAction, useState } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import { Task } from '../../components/atoms/task/Task';
import { ILog } from '../../interfaces/ILog';
import { IRepository } from '../../interfaces/IRepository';
import { IHistory } from '../../interfaces/IHistory';
import { Stop } from '../../components/atoms/svg/Stop';
import { Play } from '../../components/atoms/svg/Play';
import { sendDiscord } from '../../webhook';

interface Props {
    repo: IRepository;
    setLogs: Dispatch<SetStateAction<ILog[]>>;
    logs: ILog[];
    historyLimit: number;
    margin: number;
    discordWebhookUrl: string;
}

export function Tracker(props: Props) {
    const { repo, setLogs, logs, historyLimit, margin, discordWebhookUrl } = props;

    const [taskName, setTaskName] = useState('')
    const [time, setTime] = useState(repo.getTime())
    const [currentTaskName, setCurrentTaskName] = useState(repo.getCurrentTaskName())
    const [history, setHistory]: [IHistory[], Dispatch<SetStateAction<IHistory[]>>] = useState(repo.getHistory())

    const handleChange_taskName = (event: React.ChangeEvent<HTMLInputElement>) => {
        setTaskName(event.target.value);
    };

    const act = (taskName: string): void => {
        setCurrentTaskName(taskName)
        repo.setCurrentTaskName(taskName)

        const time = Date.now()
        setTime(time)
        repo.setTime(time)

        setTaskName('')

        sendDiscord(discordWebhookUrl, `[sink start] ${taskName}`)
    }

    const addHistory = (taskName: string): void => {
        const isPinned = history.filter(v => v.taskName === taskName)[0]?.isPinned
        const indexedHistory = [{ taskName: taskName, isPinned: !!isPinned }, ...history.filter(v => v.taskName !== taskName)].map((v, i) => { return { ...v, index: i } })
        const pinnedHistory = indexedHistory.filter(v => v.isPinned)
        console.log(pinnedHistory)
        const unPinnedHistory = indexedHistory.filter(v => !v.isPinned).slice(0, historyLimit - pinnedHistory.length)
        console.log(unPinnedHistory)
        const newHistory = [...unPinnedHistory, ...pinnedHistory].sort((a, b) => a.index - b.index)

        setHistory(newHistory)
        repo.setHistory(newHistory)
    }

    const switchTask = (taskName: string): void => {
        if (currentTaskName === taskName || taskName === '') { return setTaskName('') };

        if (currentTaskName !== '') { stopTask() }

        act(taskName)

        addHistory(taskName)
    }

    const stopTask = (): void => {
        const stopTime = Date.now()
        const log: ILog = {
            taskName: currentTaskName,
            start: time,
            end: stopTime,
        }
        const newLog = [log, ...logs]

        setLogs(newLog)
        repo.setLogs(newLog)

        // currentTaskName „ÅåÊõ¥Êñ∞„Åï„Çå„ÇãÂâç„Å´Discord„Å´ÈÄöÁü•„Åô„Çã
        sendDiscord(discordWebhookUrl, `[sink end] ${currentTaskName}`)

        setCurrentTaskName('')
        repo.setCurrentTaskName('')
    }

    const handleClick_actButton = (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        event.preventDefault()
        switchTask(taskName)
    }
    const handleClick_playButton = (taskName: string) => {
        switchTask(taskName)
    }
    const handleClick_pinButton = (taskName: string) => {
        const newHistory = [...history]
        newHistory.filter(task => task.taskName === taskName)[0].isPinned = !newHistory.filter(task => task.taskName === taskName)[0].isPinned
        setHistory(newHistory)
        repo.setHistory(newHistory)
    }
    const handleClick_stopButton = () => {
        stopTask()
    }


    return (
        <Box display="flex" flexDirection="column" padding='8px 0px'>
            <Box display="flex" minHeight="36px" margin="8px">
                <Box width="64px">
                    <Button onClick={handleClick_stopButton} disabled={currentTaskName === ''}><Stop /></Button>
                </Box>
                <Box marginY="auto" style={{ lineBreak: "anywhere" }}>
                    <Box>Task: {currentTaskName !== '' ? currentTaskName : 'break now...üçµ'}</Box>
                </Box>
            </Box>
            <Box display="flex" margin="8px" flexDirection="row">
                <Box component="form" style={{ width: "100vw", paddingRight: "8px", flexDirection: "row", display: "flex" }}>
                    <Box marginY="auto" marginX="0px" width="64px"><Button type="submit" onClick={handleClick_actButton}><Play /></Button></Box>
                    <TextField
                        id="standard-basic"
                        variant="outlined"
                        value={taskName}
                        onChange={handleChange_taskName}
                        fullWidth={true}
                        label="new task" />
                </Box>
            </Box>
            <Box display="flex" flexDirection="column" margin="8px">
                {history.map(t => <Task key={t.taskName} task={t} actHandler={handleClick_playButton} pinHandler={handleClick_pinButton} margin={margin}></Task>)}
            </Box>
        </Box>
    );
}

export default Tracker
